import Vue from 'vue'
import Vuex from 'vuex'
import firebase from 'firebase'
import router from '../router/router'

Vue.use(Vuex)

const store = new Vuex.Store({
    state: {
        user: null,
        userDetails: {
            name : '',
            title: ''
        }
    },
    mutations: {
        setUser(state, payload) {
            state.user = payload
        },
        setUserDetails(state, val) {
            state.userDetails = val
        }
    },
    actions: {
        signUserUp({commit}, payload) {
            firebase.auth().createUserWithEmailAndPassword(payload.email, payload.password)
                .then(
                    firebaseUser => {
                        const newUser = {
                            uid: firebaseUser.user.uid,
                            email: firebaseUser.user.email
                        }
                        commit('setUser', newUser)
                        firebase.firestore().collection("users").doc(newUser.uid).set({
                            name: payload.name,
                            title: payload.title
                        }).then(() => {
                            commit('setUserDetails', {name:payload.name, title:payload.title})
                            router.push('/home')
                        }).catch(err => {
                            alert(err)
                        })
                    }
                )
                .catch(
                    error => {
                        alert(error)
                    }
                )
        },
        signUserIn({commit}, payload) {
            firebase.auth().signInWithEmailAndPassword(payload.email, payload.password)
                .then(
                    firebaseUser => {
                        const newUser = {
                            uid: firebaseUser.user.uid,
                            email: firebaseUser.user.email
                        }
                        commit('setUser', newUser)
                        store.dispatch('fetchUserDetails', newUser.uid)
                        router.push('/home')
                    }
                )
                .catch(
                    error => {
                        alert(error)
                    }
                )
        },
        autoSignIn({commit}, payload) {
            commit('setUser', {user: payload})
        },
        logOut({commit}) {
            firebase.auth().signOut()
            commit('setUser', null)
            router.push('/login')
        },
        fetchUserDetails({commit}, uid) {
            firebase.firestore().collection("users").doc(uid).get().then(doc => {
                commit('setUserDetails', {name: doc.data().name , title: doc.data().title})
            }).catch(err => {
                alert(err)
            })
        }
    },

    getters: {
        user(state) {
            return state.user
        },
        userDetails(state) {
            return state.userDetails
        }
    }
})

export default store
