<template>
    <v-app>
        <nav v-if="isAuthenticated">
            <v-toolbar app
                       fixed="true"
            >
                <v-toolbar-side-icon @click="drawer = !drawer" class="grey--text"></v-toolbar-side-icon>
                <v-toolbar-title class="text-uppercase grey--text">
                    <span class="font-weight-light"></span>
                    <span>{{userDetails.name}}</span>
                </v-toolbar-title>
                <v-spacer></v-spacer>
                <v-btn flat color="grey" @click="logout">
                    <span>Sign Out</span>
                    <v-icon right>exit_to_app</v-icon>
                </v-btn>
            </v-toolbar>

            <v-navigation-drawer app v-model="drawer" class="white">
                <v-list class="pt-0" dense>
                    <v-divider></v-divider>

                    <v-list-tile
                            v-for="item in items"
                            :key="item.title"
                            :to="item.path"
                    >
                        <v-list-tile-action>
                            <v-icon>{{ item.icon }}</v-icon>
                        </v-list-tile-action>

                        <v-list-tile-content>
                            <v-list-tile-title class="grey--text text--darken4">{{ item.title }}</v-list-tile-title>
                        </v-list-tile-content>
                    </v-list-tile>
                </v-list>
            </v-navigation-drawer>
        </nav>


        <v-content>
            <router-view></router-view>
        </v-content>

    </v-app>
</template>

<script>


    export default {
        data() {
            return {
                drawer: true,
                items: [
                    {title: 'Home', icon: 'dashboard', path: '/'},
                    {title: 'Booking', icon: 'question_answer', path: '/booking'}
                ]
            }
        },
        computed: {
            isAuthenticated() {
                return this.$store.getters.user != null || this.$store.getters.user != undefined
            },

            userDetails() {
                return this.$store.getters.userDetails
            }
        },
        methods: {
            logout: function () {
                this.$store.dispatch('logOut');
            }
        }
    }
</script>